* {
  margin: 0;
  padding: 0;
}

.shoplist_header {
  position: relative;
  padding-bottom: .8rem;
  color: #fff;
  font-size: .293333rem;
}

.shoplist_header .activity_cut_box {
  position: absolute;
  left: .4rem;
  right: .4rem;
  bottom: .133333rem;
}

.shoplist_header .activity_cut_box .activity_cut_more {
  position: absolute;
  top: .066667rem;
  right: 0;
}

.shoplist_header .activity_cut_box .activity_cut_more:after {
  content: '';
  display: block;
  border-style: solid;
  border-width: .066667rem 0 .066667rem .08rem;
  border-color: transparent transparent transparent #fff;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  right: -0.16rem;
}

.shoplist_header .activity_cut_box .activity_cut {
  line-height: .48rem;
  display: -ms-flexbox;
  display: flex;
  font-size: .346667rem;
  -ms-flex-align: center;
  align-items: center;
  padding-right: 1.28rem;
}

.shoplist_header .activity_cut_box .activity_cut .activity_cut_icon {
  margin-right: .16rem;
  font-size: .293333rem;
  font-style: normal;
  height: .373333rem;
  width: .373333rem;
  display: inline-block;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  text-align: center;
  border-radius: .053333rem;
  display: -ms-flexbox;
  display: flex;
