  flex: 1 1;
  font-size: .293333rem;
}

.shoplist_header .shoplist_header_bg {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: #3190e8;
  background-size: cover;
  background-repeat: no-repeat;
}

.shoplist_header .shoplist_header_bg:before {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(119, 103, 137, 0.43);
}

.shoplist_header nav {
  position: relative;
  padding: .106667rem .266667rem;
}

.shoplist_header nav .back {
