.index_footer {
  height: 1.6rem;
}

.index_footer .index_footer_con {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: justify;
  justify-content: space-between;
  background: #fff;
  height: 1.333333rem;
  -webkit-box-shadow: 0 -1PX 1PX rgba(0, 0, 0, 0.1);
  box-shadow: 0 -1PX 1PX rgba(0, 0, 0, 0.1);
  font-size: 0;
}

.index_footer .index_footer_a {
  -ms-flex: 1 1;
  flex: 1 1;
  text-align: center;
}

.index_footer .index_footer_a a {
  display: block;
  width: 100%;
  height: 100%;
}

.index_footer .index_footer_icon {
  width: 0.533333rem;
  height: 0.533333rem;
}

.index_footer .index_footer_word {
  display: block;
  font-size: 0.32rem;
  color: #666;
  margin-top: 0.013333rem;
  -webkit-transform: scale(0.9);
  -ms-transform: scale(0.9);
  transform: scale(0.9);
}

.index_footer .active g,
.index_footer .active path,
.index_footer .active .index_footer_word {
  stroke: #2295FF!important;
  color: #2295FF;
}

.index_footer .active .first path {
  fill: #2295FF!important;
}


