.message {
  background: #fff;
  width: 100%;
  font-size: .426667rem;
  color: #333;
  list-style: none;
  cursor: pointer;
}

.message li {
  position: relative;
  padding: .266667rem .373333rem;
  cursor: pointer;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -ms-flex-align: center;
  align-items: center;
}

.message .message_right {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  color: #666;
}

.message .message_right .message_right_img {
  height: 1.333333rem;
  width: 1.333333rem;
  border-radius: .6rem;
}

.message .message_right .message_right_img svg {
  width: 100%;
  height: 100%;
}

.message .message_right input {
  display: block;
  position: absolute;
  opacity: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  outline: 0;
  border: 0;
}

.message .message_right svg {
  fill: #ddd;
  height: .48rem;
  width: .48rem;
  display: inline-block;
  line-height: 1.2rem;
  padding-top: .133333rem;
  margin-left: 0.133333rem;
}

.message .setPassword {
  color: #0097ff;
}

.message .message_phone_icon {
  padding: .053333rem;
  border-radius: .106667rem;
  margin-right: .133333rem;
  vertical-align: middle;
}

.message .message_phone_icon svg {
  height: .426667rem;
  width: .426667rem;
}

.message_fg {
  margin: .426667rem 0 .186667rem .373333rem;
  font-size: .32rem;
  color: #999;
}

.log_out {
  display: block;
  width: 100%;
  text-align: center;
  padding: .373333rem 0;
  margin: .533333rem 0;
  color: #ff5339;
  border-radius: .08rem;
  font-size: .426667rem;
  font-weight: 700;
  background: #fff;
  outline: 0;
  border: none;
}


