.login {
  padding-top: 1.066667rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-direction: column;
  flex-direction: column;
}

.login .login_bg {
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: #fff;
}

.login .login_logo {
  text-align: center;
}

.login .login_logo svg {
  width: 3.733333rem;
  height: 1.493333rem;
}

.login .login_method {
  margin-top: 1.066667rem;
  font-size: 0;
}

.login .login_method a {
  font-size: 0.373333rem;
  padding-bottom: 0.053333rem;
  cursor: pointer;
  color: #000;
}

.login .login_method a:first-child {
  margin-right: 1.066667rem;
}

.login .login_method .login_method_cur {
  color: #2395ff;
  font-weight: 700;
  border-bottom: 0.053333rem solid #2395ff;
}

.login form {
  width: 8rem;
}

.login .login_now {
  display: block;
  width: 100%;
  height: 1.12rem;
  margin-top: 0.8rem;
  border-radius: 0.106667rem;
  background-color: #4cd96f;
  color: #fff;
  cursor: pointer;
  text-align: center;
  font-size: 0.426667rem;
  line-height: 1.12rem;
  outline: none;
  border: none;
}

.login .login_tips {
  margin-top: 0.32rem;
  color: #999;
  font-size: 0.373333rem;
  line-height: 0.533333rem;
}

.login .login_tips a {
  color: #2395ff;
}

.login .login_sec {
  position: relative;
  margin-top: 0.426667rem;
  height: 1.28rem;
  font-size: 0.373333rem;
  background-color: #fff;
}

.login .login_sec input:focus {
  border-color: #0089dc;
}

.login .login_sec .get_yzm {
  padding: 0;
  border-radius: 0.16rem;
  color: #2395ff;
  background: transparent;
  text-align: center;
  font-size: 0.373333rem;
  display: inline-block;
  position: absolute;
  top: 50%;
  right: 0.133333rem;
  outline: 0;
  border: none;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.login .login_sec .get_yzm[disabled] {
  color: #ccc;
}

.login .login_sec input {
  width: 100%;
  height: 100%;
  left: 0;
  padding-left: 0.266667rem;
  border: 1PX solid #ddd;
  border-radius: 0.106667rem;
  color: #333;
  outline: none;
}

.login_wran {
  position: fixed;
  bottom: 2.56rem;
  left: 50%;
  z-index: 9999;
  padding: .266667rem .533333rem;
  border-radius: .533333rem;
  background: rgba(0, 0, 0, 0.6);
  color: #fff;
  font-size: .32rem;
  line-height: 1.2;
  -webkit-transition: opacity .5s;
  -o-transition: opacity .5s;
  transition: opacity .5s;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  pointer-events: none;
}

.out {
  opacity: 0;
}


