.index_header {
  padding: 0.266667rem 0.266667rem 0;
  color: #fff;
  background-image: -webkit-gradient(linear, left top, right top, from(#0af), to(#0085ff));
  background-image: -webkit-linear-gradient(left, #0af, #0085ff);
  background-image: -o-linear-gradient(left, #0af, #0085ff);
  background-image: linear-gradient(90deg, #0af, #0085ff);
}

.index_header .header_title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
  height: 0.92rem;
  -ms-flex-align: center;
  align-items: center;
}

.index-address {
  width: 60%;
  font-weight: 700;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
}

.index-address .address_icon {
  width: 0.4rem;
  height: 0.466667rem;
  background: url('address.png') no-repeat center;
  background-size: cover;
}

.index-address .sj {
  border-style: solid;
  border-width: 0.08rem;
  border-color: transparent;
  border-top-color: #fff;
}

.index-word {
  margin: 0 0.133333rem;
  font-size: .48rem;
  max-width: 80%;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
}

.weather {
  display: -ms-flexbox;
  display: flex;
  text-align: right;
  -ms-flex-align: center;
  align-items: center;
}

.weather .weather_today {
  font-size: 0.32rem;
  -webkit-transform: scale(0.8);
  -ms-transform: scale(0.8);
  transform: scale(0.8);
}

.weather .weather_num {
  font-size: 0.373333rem;
}

.weather .weather-icon {
  margin-left: 0.066667rem;
  width: 0.733333rem;
  height: 0.733333rem;
}


