.use_header {
  height: 1.173333rem;
}

.use_header .use_header_bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
  background-image: -webkit-gradient(linear, left top, right top, from(#0af), to(#0085ff));
  background-image: -webkit-linear-gradient(left, #0af, #0085ff);
  background-image: -o-linear-gradient(left, #0af, #0085ff);
  background-image: linear-gradient(90deg, #0af, #0085ff);
}

.use_header .use_header_bg .back {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  width: 1.173333rem;
  height: 1.173333rem;
}

.use_header .use_header_bg .back svg {
  width: 0.586667rem;
  height: 0.586667rem;
}

.use_header .use_header_bg .use_header_title {
  position: absolute;
  top: 0;
  left: 50%;
  height: 1.173333rem;
  max-width: 50%;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  font-weight: 700;
  font-size: 0.44rem;
  line-height: 1.173333rem;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  color: #fff;
}


