.banner {
  position: relative;
}

.banner_list {
  height: 4.72rem;
  background: #fff;
  text-align: center;
  overflow: hidden;
}

.banner_list a {
  position: relative;
  float: left;
  margin-top: 0.293333rem;
  width: 25%;
}

.banner_list .container {
  display: inline-block;
  position: relative;
  width: 1.2rem;
  height: 1.2rem;
}

.banner_list .container img {
  width: 100%;
  height: 100%;
  vertical-align: top;
  max-width: 100%;
}

.banner_list .title {
  display: block;
  margin-top: 0.133333rem;
  color: #666;
  font-size: 0.32rem;
}

.baner_dd {
  position: absolute;
  left: 50%;
  bottom: 0.133333rem;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  width: 100%;
  text-align: center;
  font-size: 0;
}

.baner_dd li {
  display: inline-block;
  width: 0.193333rem;
  height: 0.193333rem;
  margin: 0 0.066667rem;
  background: #000;
  opacity: .2;
  border-radius: 50%;
}

.baner_dd li.is-active {
  background-color: #000;
  opacity: .6;
}


