.my .my_unlogin {
  display: -ms-flexbox;
  display: flex;
  background: -webkit-gradient(linear, left top, right top, from(#0af), to(#0085ff));
  background: -webkit-linear-gradient(left, #0af, #0085ff);
  background: -o-linear-gradient(left, #0af, #0085ff);
  background: linear-gradient(90deg, #0af, #0085ff);
  color: #fff;
  -ms-flex-align: center;
  align-items: center;
  padding: .666667rem .4rem;
}

.my .my_unlogin .my_unlogin_img {
  width: 1.6rem;
  height: 1.6rem;
  background-color: #fff;
  border-radius: 50%;
}

.my .my_unlogin .my_unlogin_img svg {
  width: 1.6rem;
  height: 1.6rem;
}

.my .my_unlogin .my_unlogin_go {
  width: .266667rem;
  height: .56rem;
  font-size: 0;
}

.my .my_unlogin .my_unlogin_sub {
  margin-left: .48rem;
