.unlogin_activity a {
  font-size: 0;
  display: table-cell;
  width: 33.3333%;
  vertical-align: middle;
  text-align: center;
}

.unlogin_activity a:not(:last-child) {
  border-right: 1PX solid #ddd;
}

.unlogin_activity a svg {
  width: .693333rem;
  height: .693333rem;
}

.unlogin_activity a .unlogin_activity_word {
  margin-top: .133333rem;
  font-size: .32rem;
  line-height: .373333rem;
  color: #666;
  font-weight: 700;
}


